import { useParams, useNavigate } from 'react-router-dom';
import GamePlayer from '../../components/GamePlayer';
import { useEffect, useState } from 'react';

export default function GamePage() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Ê£ÄÊü•Ê∏∏ÊàèÊñá‰ª∂ÊòØÂê¶Â≠òÂú®
    fetch(`/games/${decodeURIComponent(id || '')}.html`)
      .then(response => {
        if (!response.ok) {
          throw new Error('Game not found');
        }
        setIsLoading(false);
      })
      .catch(() => {
        navigate('/'); // Â¶ÇÊûúÊ∏∏Êàè‰∏çÂ≠òÂú®ÔºåËøîÂõûÈ¶ñÈ°µ
      });
  }, [id, navigate]);

  if (isLoading) {
    return <div>Loading...</div>;
  }

  // Ê†πÊçÆË∑ØÁî±ÂèÇÊï∞Ëé∑ÂèñÂØπÂ∫îÁöÑÊ∏∏Êàè‰ø°ÊÅØ
  const gameInfo = {
    title: decodeURIComponent(id || ''),
    gameUrl: `/games/${decodeURIComponent(id || '')}.html`
  };

  return <GamePlayer {...gameInfo} />;
} 